import Component from "vue-class-component";
import axios from "axios";
import * as UserApi from "../../api/UserApi";
class addressList {}
@Component({
  props: {},
  components: {
    // categoryView,
  }
})
export default class AddGoods extends Vue {
  userInfo = {
    token: "",
    userId: ""
  };
  addressList = [];
  goodsList = [];
  favPage = 0;
  favtotal = 0;
  pageSize = 20;
  bigIcon = "";
  viewBig = false;
  onfavPageChange(page) {
    this.favPage = page - 1;
    this.queryFav();
  }
  getImageUrl(url: string) {
    if (url == null) {
      return "";
    }
    let arrayUrl = url.split(",");
    if (arrayUrl.length > 0) {
      return arrayUrl[0];
    }

    return url;
  }
  queryUserAddress() {
    UserApi.queryUserAddress({
      token: this.userInfo.token,
      userId: this.userInfo.userId
    }).then(res => {
      if (res["retCode"]) {
        this.addressList = res.data.addressList;
        console.log(res);
      } else {
        console.error("数据查询错误");
      }
    });
  }
  queryFav() {
    UserApi.queryFav({
      token: this.userInfo.token,
      userId: this.userInfo.userId,
      page: this.favPage,
      pageSize: 20
    }).then(res => {
      if (res["retCode"]) {
        console.log("收藏", res);
        this.favtotal = res.data.page.total;
        this.goodsList = res.data.goodsList;
      } else {
        console.error("数据查询错误");
      }
    });
  }
  viewBigIcon(url) {
    this.bigIcon = url;
    this.viewBig = true;
  }
  mounted() {
    if ((sessionStorage.member || "") != "") {
      this.userInfo = JSON.parse(sessionStorage.member);
      console.log(this.userInfo);
    } else {
      this.$alert("找不到该会员");
      this.$router.go(-1);
      return;
    }
    this.userInfo.token = this.userInfo.token;
    this.userInfo.userId = this.userInfo.userId;

    this.queryUserAddress();
    this.queryFav();
  }
}



