import Component from "vue-class-component";
import axios from "axios";
import * as sysApi from "../../api/sysApi";
import * as Api from "../../api/api";

@Component({
  props: {},
  components: {
    // categoryView,
  }
})
export default class AddGoods extends Vue {
  merchantUserList = [];
  pageSize = 10;
  total = 100;
  currentPage = 0;
  model = false;
  password = "";
  repassword = "";
  obj = {};
  modelObj = {
    loginName: "",
    roleId: ""
  };

  doDelete(row) {
    Api.doConfirm(this, "是否删除该账户").then(res => {
      if (res) {
        sysApi.deleteUser({ userId: row.merchantUserId }).then(res => {
          if (res["retCode"]) {
            console.log(res);
            this.$message("删除成功");
            this.queryList();
          } else {
            if(!res['islogin']){this.$alert(res["message"]);}
            console.error("数据查询错误");
          }
        });
      }
    });
  }

  doSubmit() {
    console.log(this.obj);
    if ((this.obj["nickName"] || "") == "") {
      this.$message("请填写备注");
      return;
    }
    if ((this.obj["loginName"] || "") == "") {
      this.$message("请填写登录名");
      return;
    }
    if ((this.password || "") == "" || (this.repassword || "") == "") {
      this.$message("请填写密码或者两次密码不匹配");
      return;
    }
    if ((this.obj["roleId"] || "") == "") {
      this.$message("请选择一个角色");
      return;
    }

    this.obj["password"] = this.password;
    if (this.type == "add") {
      console.log("添加商户");
      sysApi.addUser(this.obj).then(res => {
        if (res["retCode"]) {
          console.log(res);
          this.$message("添加成功");
          this.changeModel(null, null);
          this.queryList();
        } else {
          if(!res['islogin']){this.$alert(res["message"]);}

          console.error("数据查询错误");
        }
      });
    }

    if (this.type == "edit") {



  sysApi.updateUser(this.obj).then(res => {
        if (res["retCode"]) {
          console.log(res);
          this.$message("保存成功");
          this.changeModel(null, null);
          this.queryList();
        } else {
          if(!res['islogin']){this.$alert(res["message"]);}

          console.error("数据查询错误");
        }
      });

    }

    // addUser
  }
  type = "add";
  changeModel(type, obj) {
    if (type == "add") {
      this.obj = {
        loginName: "",
        password: "",
        roleId: "",
        nickName: ""
      };
      this.password = "";
      this.repassword = "";

      this.type = type;
    } else if (type == "edit") {
      let a = {};
      Object.assign(a, obj);
      this.obj = a;
      this.password = obj.password;
      this.repassword = obj.password;
      this.type = type;
    }
    this.model = !this.model;
  }
  onPageChange(currentPage: number) {
    this.currentPage = currentPage;
    this.queryList();
  }
  queryList() {
    sysApi.queryUserList(this.currentPage, this.modelObj).then(res => {
      if (res["retCode"]) {
        this.merchantUserList = res.data.merchantUserList;
        this.total = res.data.page.total;

        console.log(res);
      } else {
        if(!res['islogin']){this.$alert(res["message"]);}

        console.error("数据查询错误");
      }
    });
    console.log("查询列表");
  }
  roleList = [];
  queryRoleList() {
    sysApi.queryRoleList().then(res => {
      if (res["retCode"]) {
        console.log(res.data);
        this.roleList = res.data;
      } else {
        console.error("数据查询错误");
      }
    });
  }

  mounted() {
    this.queryRoleList();
    this.queryList();
    console.log("页面加载");
  }
}



