import Component from "vue-class-component";
import axios from "axios";
import * as UserApi from "../../api/UserApi";

@Component({
  props: {},
  components: {
    // categoryView,
  }
})
export default class AddGoods extends Vue {
  recordList = [];
  recordIds = [];
  pageSize = 10;
  total = 0;
  currentPage = 0;
  formatStatus(status){
    switch(status){
      case 'APPLY':
      return '未处理';
         case 'ACCEPT':
      return '已提';
         case 'REFUSE':
      return '拒绝';
      default : 
      return '';
    }
  }
  onPageChange(currentPage: number) {
  
    this.currentPage = currentPage-1;
    this.queryList();
  }
  doAccpset(recordIds) {
    this.$confirm("确认受理？", "提示", {
      confirmButtonText: "同意提现",
      cancelButtonText: "拒绝提现",
      type: "warning",
        center: true
    })
      .then(() => {
        UserApi.doAccpset(recordIds.join(","),'ACCEPT').then(res => {
          if (res["retCode"]) {
            this.$message("处理成功");
            this.queryList();
          } else {
            console.error("数据查询错误");
          }
        });
      })
      .catch(e => {
        console.log(e);

 UserApi.doAccpset(recordIds.join(","),'REFUSE').then(res => {
          if (res["retCode"]) {
            this.$message("已拒绝");
            this.queryList();
          } else {
            console.error("数据查询错误");
          }
        });


      });




  }
  status=''
  queryList() {
    UserApi.getRecordList(this.currentPage,this.status).then(res => {
      if (res["retCode"]) {

        console.log(res.data.page.total)
        this.total = res.data.page.total;
        this.recordList = res.data.recordList;
      } else {
        console.error("数据查询错误");
      }
    });
    console.log("查询列表");
  }

  mounted() {
    this.queryList();
    console.log("页面加载");
  }
}



