import Component from "vue-class-component";
import axios from "axios";
import * as sysApi from "../../api/sysApi";
import * as Api from "../../api/api";

@Component({
  props: {},
  components: {
    // categoryView,
  }
})
export default class AddGoods extends Vue {
  roleList = [];

  checked3 = true;
  checked4 = false;
  checked5 = false;
  checked6 = true;
  checkboxGroup5 = [];
  model = false;

  obj = {};
  type = "add";
  changeModel(type, row) {
    this.checkboxGroup5 = [];
    this.type = type;
    if (this.type == "add") {
      this.obj["roleName"] = "";
    } else {
      let a = {};
      Object.assign(a, row);
      this.obj = a;
      this.queryPermissionIds(this.obj["id"]);
    }
    this.model = !this.model;
  }
  doDelete(row) {
    Api.doConfirm(this, "是否需要删除该角色!").then(res => {
      if (res) {
        sysApi
          .deleteRole({
            roleId: row.id
          })
          .then(res => {
            if (res["retCode"]) {
              this.queryList();
              this.$message("删除成功");
            } else {
              if(!res['islogin']){this.$alert(res["message"]);}
              console.error("数据查询错误");
            }
          });
      }
    });
  }
  queryPermissionIds(roleId) {
    sysApi
      .queryPermissionIds({
        roleId: roleId
      })
      .then(res => {
        if (res["retCode"]) {
          console.log("查询角色权限", res.data);
          this.checkboxGroup5 = this.checkboxGroup5 ? this.checkboxGroup5 : [];
          res.data.forEach((item, index) => {
            this.checkboxGroup5.push(item.permissionId);
          });
        } else {
          if(!res['islogin']){this.$alert(res["message"]);}
          console.error("数据查询错误");
        }
      });
  }
  changePermissionIds(roleId) {
    sysApi
      .changePermissionIds({
        roleId: roleId,
        permissionIds: this.checkboxGroup5.join(",")
      })
      .then(res => {
        if (res["retCode"]) {
          this.$message("权限配置已保存");
        } else {
          if(!res['islogin']){this.$alert(res["message"]);}
          console.error("数据查询错误");
        }
      });
  }

  doSubmit() {
    if (this.type == "add") {
      sysApi.doRoleAdd(this.obj["roleName"]).then(res => {
        if (res["retCode"]) {
          console.log(res.data);
          this.changePermissionIds(res.data.id);
          this.$message("添加成功");
          this.model = !this.model;
          this.queryList();
        } else {
          if(!res['islogin']){this.$alert(res["message"]);}
          console.error("数据查询错误");
        }
      });
    } else {
      sysApi.doRoleUpdate(this.obj).then(res => {
        if (res["retCode"]) {
          console.log();
          this.changePermissionIds(this.obj["id"]);
          this.$message("修改成功");
          this.model = !this.model;
          this.queryList();
        } else {
          if(!res['islogin']){this.$alert(res["message"]);}
        }
      });
    }
  }
  queryList() {
    sysApi.queryRoleList().then(res => {
      if (res["retCode"]) {
        console.log(res.data);
        this.roleList = res.data;
      } else {
        console.error("数据查询错误");
      }
    });
    console.log("查询列表");
  }

  mounted() {
    this.queryList();
    console.log("页面加载");
  }
}



