
































































import Vue from "vue";
import Component from "vue-class-component";
import axios from "axios";
import * as UserApi from "../../api/UserApi";
import * as Api from "../../api/api";
@Component({
  props: {},
  components: {
    // categoryView,
  }
})
export default class AddGoods extends Vue {
  hotwordList = [];
  obj = {};
  model = false;
  type = "add";
  changeModel(type, row) {
    if (type == "add") {
      this.obj = {};
      this.type = type;
    }
    if (type == "edit") {
      let a = {};
      Object.assign(a, row);
      this.obj = a;
      this.type = type;
    }
    this.model = !this.model;
  }

  queryList() {
    UserApi.getHotword().then(res => {
      if (res.status == 200) {
        console.log(res.data);
        this.hotwordList = res.data.data;
      } else {
        console.error("数据查询错误");
      }
    });
    console.log("查询列表");
  }
  doSubmit() {
    if ((this.obj["word"] || "") == "") {
      this.$message("请输入热词");
      return;
    }
    if (this.type == "add") {
      UserApi.addHotWord(this.obj).then(res => {
        if (res["retCode"]) {
          this.$message("添加成功");
          this.changeModel(null, null);
          this.queryList();
        } else {
          if(!res['islogin']){this.$alert(res["message"]);}
          console.error("数据查询错误");
        }
      });
    }
    if (this.type == "edit") {
      UserApi.updateHotWord(this.obj).then(res => {
        if (res["retCode"]) {
          this.$message("更新成功");
          this.changeModel(null, null);
          this.queryList();
        } else {
          if(!res['islogin']){this.$alert(res["message"]);}
          console.error("数据查询错误");
        }
      });
    }
  }
  doDelete(row) {
    Api.doConfirm(this, "是否删除该热词").then(res => {
      if (res) {
        UserApi.deleteHotWord({ id: row.id }).then(res => {
          if (res["retCode"]) {
            this.$message("删除成功");
            this.queryList();
          } else {
            if(!res['islogin']){this.$alert(res["message"]);}
            console.error("数据查询错误");
          }
        });
      }
    });
  }
  mounted() {
    this.queryList();
    console.log("页面加载");
  }
}



