import Component from "vue-class-component";
import axios from "axios";
import * as UserApi from "../../api/UserApi";
import moment from "moment";
@Component({
  props: {},
  components: {
    // categoryView,
  }
})
export default class AddGoods extends Vue {
  feedbackList = [];
  total = 0;
  pageSize = 20;
  currentPage = 0;
  keyword = "";
  lanNum: string = "";
  onPageChange(currentPage: number) {
    this.currentPage = currentPage - 1;
    this.queryList();
  }
  changeModel(id) {
    this.lanNum == id ? (this.lanNum = "") : (this.lanNum = id);
  }
beginDate= ''
endDate = ''
  queryList() {
      if ((this.beginDate || "") != "") {
     this.beginDate =
        moment(this.beginDate).format("YYYY-MM-DD") + " 00:00:00";
    }

    if ((this.endDate || "") != "") {
     this.endDate =
        moment(this.endDate).format("YYYY-MM-DD") + " 23:59:59";
    }
    
    UserApi.queryFeedBack(this.currentPage, this.keyword,this.beginDate,this.endDate).then(res => {
      if (res["retCode"]) {
        this.feedbackList = res.data.feedbackList;
        this.total = res.data.page.total;
      } else {
        console.error("数据查询错误");
      }
    });
    console.log("查询列表");
  }

  mounted() {
    this.queryList();
    console.log("页面加载");
  }
}



